.login_page_loginForm{
    max-width: 20%;
    margin: 0 auto;
}
.article_page_detail{
    max-width: 60%;
    margin: 0 auto;
}
.register-page-regCom{
    max-width: 50%;
    margin: 0 auto;
}
.write_article_Com{
    width:60%;
    margin: 0 auto;
}
.articlecom{
    width:800px;
    margin: 0 auto;
}
.article-info-div{
    margin-top:1px;
    margin-bottom: 10px;
}

.author_page_content{
    width: 1000px;
    margin: 0 auto;
}
.personal_setting_Com{
    width: 60%;
    margin: 0 auto;
}



.article_page_title{
    text-align: center;
}
.article_page_author_name{
    font-weight: bold;
}
.article_author_avatar{
    border:1px solid rgb(220, 220, 220);
}
.commentscomponent{
    margin: 0 auto;
    border:1px solid rgb(235,235,235);
    box-shadow: 0px 0px 10px #a3a2a2;
    background: rgb(255, 255, 255); 
    padding-bottom: 5px;
} 
.commentsTitle{
    padding-left: 10px;
    font-size: 18px;
    font-weight: 700;
}
.singlecomment{
    margin:-10px 0 -10px -30px;
}
.commentFoot{
    cursor:pointer;
}
.comment-pagination{
    margin-right: 4%;
    text-align: right;
}

.articlesingle {
    border-radius:2px;
    box-shadow: 0px 0px 8px #a3a2a2;
    background: rgb(255, 255, 255); 
    padding:0 3px ; 
    min-height: 130px;
    /* max-height: 130px; */
}
.articlesingle_comments{
    margin:10px 0 0 0 ;
}
.article-title{
    height:25px;
    font-size: 18px;
    font-weight: 700;
    line-height: 1.5;
}
.article-breif{
    height:75px;
    padding: 2px auto 4px auto;
    text-align: justify;
    text-justify:inter-ideograph;
    overflow:hidden;
    text-overflow: ellipsis;
}
.article-note{
    height:10px;
}
.article-url-link{
    text-decoration:none;
    color: black;
}
.article-url-link:hover{
    text-decoration:none;
    color: black;
}
.article-url-link:active{
    text-decoration:none;
    color: black;
}
.article-url-link:link{
    text-decoration:none;
    color: black;
}
.articleCover{
    margin:5px 5px 5px auto;
    height: 120px;
    max-width:100%;
    max-height:90%;
}
.loadMore{
    margin: 0 auto;
    text-align: center;
    cursor: pointer;
}
.likes_article{
    cursor: pointer;
}
.comment_article{
    cursor: pointer;
}
.edit_article_com{
    cursor: pointer;
}
.login-form-forgot {
    float: right;
    color: rgb(24,144,255);
    cursor: pointer;
  }
.login-form-button {
    width: 100%;
}
.forgetCom_resetButton{
  width: 100%;
}
.popForgetCom{
  margin-top: 20px;
}
.regiserCom_regButton{
  width: 100%;
}

.get_email_code_label{
  cursor: pointer;
}
.author_avatar{
    border:1px solid rgb(220, 220, 220);
}
.author_introduce_tag{
    margin: 2px 4px 6px 4px;
}
.personal_Com{
    margin:0 auto;
    background-color: white;
}

/* .personal_Menu{
    border-right: 1px solid red;
} */

.userbaseCom_username{
    margin-left: 10px;
}

.userbaseCom_changeavatar_label{
    background: rgb(64,169,255);
    border-radius: 2px;
    color: white;
    padding: 2px 3px;
    cursor: pointer;
}
.userbaseCom_changeavatar{
    display:none;
}

.userbase_info{
    margin:2px auto;
}



body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(246,246,246);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

